body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  text-align: center;
  margin-top: 19px;
}

.app-container {
  background-color: rgba(252, 252, 252, 1);
 padding: 20px;
 margin: 100px 0px 0px 303px;
 width: 60%;
 box-shadow: 1px 2px 1px #aaaaaa;
}

h4 {
  margin: 20px 10px;
}

h4 a {
  transition: letter-spacing 0.3s;
}

h4 a:hover {
  text-decoration: none;
  letter-spacing: 0.2em;
}

td a {
  transition: letter-spacing 0.3s;
}

td a:hover {
  text-decoration: none;
  letter-spacing: 0.1em;
}


.Edit {
    text-align: center;
    margin-top: 10px;
}

.edit-container {
    background-color: rgba(252, 252, 252, 1);
    padding: 20px;
    margin: 100px 0px 0px 303px;
    width: 60%;
    box-shadow: 1px 2px 1px #aaaaaa;
}

.edit-input {
    width: 90%;
    padding: 12px 20px;
    margin: 8px 0;
    display: inline-block;
    box-sizing: border-box;
}

.edit-button {
    background: #06A142;
    color: white;
    padding: 14px 20px;
    margin: 8px 8px;
    border: none;
    cursor: pointer;
    transition: box-shadow 0.3s;
    text-align: center;
}

  .edit-button:hover {
    box-shadow: 2px 2px 3px #727272;
    text-decoration: none;
    color: #fff;
}
